nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    background-color: #333;
}

nav li {
    display: inline;
}

nav a {
    display: inline-block;
    padding: 15px 28px;
    text-decoration: none;
    color: white;
}

nav a.active {
    background-color: #364FAD;
}

nav a:not(.active):hover {
    background-color: #000;
}

main {
    padding: 0 15px;
}
